.roulette-surface {
    display: flex;
    flex-wrap: nowrap; /* Assicura che tutti i numeri rimangano su una singola riga */
    gap: 2px; /* Spazio ridotto tra i numeri */
  }
  
  .roulette-number {
    width: 40px; /* Dimensioni ridotte */
    height: 40px; /* Dimensioni ridotte */
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    cursor: pointer;
    font-size: 1em; /* Dimensione del testo ridotta */
  }
  
  .roulette-number.red {
    background-color: red;
    color: white;
  }
  
  .roulette-number.black {
    background-color: black;
    color: white;
  }
  
  .roulette-number.green {
    background-color: green;
    color: white;
  }
  