.sestine-table {
    margin: 20px auto;
    border-collapse: collapse;
  }
  
  .sestine-table th, .sestine-table td {
    border: 1px solid black;
    padding: 10px;
    text-align: center;
    transition: background-color 0.3s ease; /* Aggiunta della transizione */
  }
  
  .green {
    background-color: green;
    color: white;
  }
  
  .orange {
    background-color: orange;
    color: white;
  }
  
  .red {
    background-color: red;
    color: white;
  }
  
  /* Media query per schermi pi√π piccoli */
  @media (max-width: 600px) {
    .sestine-table th, .sestine-table td {
      padding: 8px;
      font-size: 0.9em;
    }
  }